const styles = theme => ({
    main: {
      width: 'auto',
      display: 'block', // Fix IE 11 issue.
      marginLeft: theme.spacing.unit * 3,
      marginRight: theme.spacing.unit * 3,
      [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
        width: 400,
        marginLeft: 'auto',
        marginRight: 'auto',
      },
      borderRadius: '5px'
    },
    squares: {
      margin: 'auto',
      alignItems: 'center',
      display: 'flex',
      float: 'left',
      color: '#000'
    },
    paper: {
      marginTop: theme.spacing.unit * 8,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'right',
      padding: `${theme.spacing.unit * 4}px ${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px`,
    },
    form: {
      width: '100%',
      marginTop: theme.spacing.unit,
    },
    submit: {
      marginTop: theme.spacing.unit * 2.5,
    },
    noAccountHeader: {
      width: '100%',
      paddingTop: '10px'
    },
    signUpLink: {
      width: '100%',
      textDecoration: 'none',
      color: '#303f9f',
      fontWeight: 'bolder',
    },
    errorText: {
      color: 'red',
      textAlign: 'center'
    }
  });
  
  export default styles;